import './Header.css';
import logo from '../images/logo.svg'
function Header() {
    return (
        <div className="Header">
            <div className='logoimg'>
                <a className="logo" href="">
                    <img src={logo} alt="Logo" />
                </a>
            </div>
            <div className="nav-and-lang">
                <nav className="top-nav">
                    <div className="desktop-menu">
                        <ul className='navigcia'>
                            <li className="navigcia-first">Контакты</li>
                            <li className="navigcia-centr">Инструкция</li>
                            <li className="navigcia-last">Условия пользования</li>
                        </ul>
                    </div>
                </nav>
                <select className='langselect' id='languageSelect'>
                    <option value='ru'>Русский</option>
                    <option value='kz'>Қазақша</option>
                </select>
            </div>
        </div>
    );
}

export default Header;
